const links = [
    {
        page: 'MAKERS',
        path: '/makers'
    },
    {
        page: 'REGIONS',
        path: '/regions'

    },
    {
        page: 'STORIES',
        path: '/stories'
    }
]

const [menuClicked , setMenuClicked] = useState(false)

const clickMenuIcon = () => {
    setMenuClicked(!menuClicked)
}

function Layout({ children }) {
    return (
        <div>
            <NavBar setMenuClicked={clickMenuIcon} logo="/logo.png" categories={categories} max_shown={8} links={links} home="/" />
            <SideMenu menuClicked={menuClicked} setMenuClicked={clickMenuIcon}/>
            {children}
        </div>
    )
}

export default Layout